/*------------------------------------------------------------------
[Fonts] 
*/

/*------------------------------------------------------------------
[Colors] 
*/
$blue: #0081f9;
$green: #28cc8b;
$darkGrey: #707070;
$grey: #969595;

/*------------------------------------------------------------------
[Variables] 
*/

/*------------------------------------------------------------------
[Resets] 
*/
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #ebebeb;
  color: #000000;
}

/*------------------------------------------------------------------
[Typography] 
*/
h1 {
  font-size: 88px;
  color: #000000;
  text-transform: uppercase;
}

/*------------------------------------------------------------------
[Utilities] 
*/
.green_text {
  color: $green !important;
}

.blue_text {
  color: $blue !important;
}
